  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


h1 {
  font-weight: 700;
  font-size: 5em;
  text-shadow: 2;
}

hr {
  width: 400px;
  border-top: 1px solid #f8f8f8;
  border-bottom: 1px solid rgba(0,0,0,0.2); 
}

.buttoncluster {
  display: grid;
  justify-content: center;
}

#content {
  text-align: center;
  padding-top: 25%;
